* {
  background-color: #0b0e0e;
  color: seashell;
  font-family: "Open Sans", sans-serif;
  font-size: 22px;
  text-align: center;
  a {
    transition: all 0.1s;
  }
  a:hover, a:focus {
    color: #DB5375;
  }
  a:active {
    color: #2D82B7;
  }
  h2 {
    font-size: 1.5rem;
    margin: 0.3rem 0.3rem 1rem;
    text-transform: uppercase;
  }
  h3 {
    font-size: 1.17rem;
  }
  h4 {
    margin: 0.5rem;
  }
}



.flex {
  background-color: unset;
  display: inline-flex;
  justify-content: space-around;
  margin: 0 auto;
  div {
    padding: 0 1.14rem;
  }
}



#navigation {
  align-items: center;
  border-bottom: 0.045rem solid seashell;
  display: flex;
  height: 2.23rem;
  left: 0;
  opacity: 0.875;
  padding: 0.45rem 0.45rem 0.41rem;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  .logo {
    height: 2.27rem;
    opacity: 1;
  }
  #navbar {
    opacity: 1;
    position: absolute;
    right: 1.15rem;
    top: auto;
    a {
      opacity: 1;
      padding: 0.45rem;
    }
  }
}



#hero {
  align-items: center;
  background: url(../assets/images/img-mission-background.webp) center no-repeat;
  border-radius: 0 0 0.5rem 0.5rem;
  display: flex;
  height: 32rem;
  justify-content: center;
  margin: 3rem auto 0;
  width: 54rem;
  .banner-text {
    opacity: 0.875;
    padding: 0.05rem;
    width: inherit;
  }
}



#totm {
  padding: 3rem 0 0;
  margin: 0 auto;
  .totm-image {
    border-radius: 0.5rem;
    height: 9rem;
    width: 14rem;
  }
  .totm-name {
    margin: 0;
    padding: 0.5rem 0;
  }
  .row {
    padding: 1rem 0 0;
  }
}



#locations {
  background: url(../assets/images/img-locations-background.webp) center no-repeat;
  border-radius: 0.5rem;
  height: 32rem;
  margin: 3rem auto 1rem;
  padding-top: 4rem;
  width: 54rem;
  .heading {
    background-color: unset;
    h2 {
      border-radius: 0.5rem;
      margin: 0 auto;
      opacity: 0.875;
      padding: 0.5rem;
      width: 22rem;
    }
  }
  .loc {
    border-radius: 0.5rem;
    margin: 1rem;
    opacity: 0.875;
    width: 14rem;
  }
}



#contacts {
  height: 9rem;
}



#copyright {
  h5 {
    padding-left: 0.5rem;
    text-align: left;
  }
}